import '../styles/Projects.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectOverlayCard from './ProjectOverlayCard';

export default function GroupedProjects(props) {
    return (
        <>
            <Row>
                <h2>{props.group}</h2>
                <hr/>
            </Row>
            <div className="project-cards">
                <Row>
                    {props.projects.map((project) => (
                        <Col md={6} key={project.key}> {/* key needs to be added to stop react error */}
                            <ProjectOverlayCard {...project}/> {/* passes through all of 'project' details */}
                        </Col>
                    ))}
                </Row>
            </div>
        </>
    )
}
