import '../../styles/Project.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectTextCard from '../ProjectTextCard';
import ProjectImageCard from '../ProjectImageCard';
import ItchDownload from '../ItchDownload';
import ProjectYoutubeCard from '../ProjectYoutubeCard';

export default function WolfWolfSheep() {
    return (
        <div className="project">
            <Row>
                <Col md={12}>
                    <ProjectTextCard>
                        <h2>Wolf Wolf Sheep</h2>
                        <h5>A Fast-Paced Asymmetrical Multiplayer Game</h5>
                    </ProjectTextCard>
                    <ProjectYoutubeCard src="jhzcG77WgVY" type="video/mp4" muted="true"/>
                    <ItchDownload src='1540140' download='https://frozenwipstudios.itch.io/wolf-wolf-sheep' title='Wolf Wolf Sheep by frozenwispstudios, Kees Tucker, minwater2' />
                </Col>
            </Row>
            
            <Row>
                {/* ------ Left Column ------ */}
                <Col md={8}>
                    <ProjectTextCard>
                        <h2>Summary</h2>
                        <br />
                        <h3>This game was submitted for Aotearoa 48h Game Jam 2022.</h3>

                        <h3>The theme for the game jam was Transition.</h3>
                        <h3>Wolf Wolf Sheep fits the theme by creating multiplayer experience around wolf players to transiting between wolf and sheep states.</h3>
                        
                        <h3> We used Photon Unity Networking library for networking, environment was made using provided synty assets</h3>
                        <h3> I implemented the basic lobby system, player movement, and shotgun mechanic.</h3>
                        <h3> I have also added assets made by the artists (models, animations) to the game.</h3>

                        <h3>I have learnt the importance of project scoping because we finished the game early and even had spare time to add extra features.</h3>

                        <h3>Unfortunately, the game was disqualified for using PUN but it was very close to winning People's Choice Award..</h3>
                    </ProjectTextCard>
                </Col>

                {/* ------ Right Column ------ */}
                <Col md={4}>
                    <ProjectTextCard>
                        <h2>Team</h2>
                        <ul>
                            <li>Me - Programmer</li>
                            <li>Ben - Programmer</li>
                            <li>Kees - Programmer</li>
                            <li>Kent - Artist</li>
                            <li>Renee - Artist</li>
                        </ul>
                    </ProjectTextCard>
                
                    <ProjectTextCard>
                        <h2>Technologies</h2>
                        <ul>
                            <li>Unity</li>
                            <li>C#</li>
                            <li>.NET</li>
                            <li>Photon</li>
                        </ul>
                    </ProjectTextCard>
                </Col>
            </Row>

            <Row> 
                <Col md={4}>
                    <ProjectImageCard src="/images/wws/wws1.png"/>
                </Col>
                <Col md={4}>
                    <ProjectImageCard src="/images/wws/wws2.png"/>
                </Col> 
                <Col md={4}>
                    <ProjectImageCard src="/images/wws/wws3.png"/>
                </Col> 
            </Row>
        </div>
    )
}
