import '../../styles/AboutMe.scss';
import {Row} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faDiscord, faItchIo, faGithub } from '@fortawesome/free-brands-svg-icons'
import { faEnvelopeSquare, faLocationArrow } from '@fortawesome/free-solid-svg-icons';

export default function AboutMe() {

    return (
        <Row className="justify-content-center">
            <div className="aboutMe" id="about">

                <div className="profile">

                    <div className="heading">
                        <h2>About Me</h2>
                    </div>

                    <div className="profile-picture img-fluid">
                        <img src="/images/About/profile.png" alt="profile-pic"></img>
                    </div>

                    <div className="description">
                        <p>Hi, I am a game programmer from New Zealand with experience in designing, developing, delivering, and maintaining 
                            interactive 3D applications for Virtual Reality, PC, and mobile devices using Unity Engine. </p>

                        <p>Here you can check out some of the professional projects I've had the opportunity to contribute, 
                        and some of the fun personal projects or game jam projects that I've worked on during spare time.</p>
                    </div>
                
                    <div className="heading">
                        <h2>Contacts</h2>
                    </div>
                </div>

                <div className="contacts">
                    <div className="social-links">
                        <a href="https://www.linkedin.com/in/minwater2/"><FontAwesomeIcon icon={faLinkedin} size="2x" color="#d8d8d8"/></a>
                        <a href="https://minwater2.itch.io/"><FontAwesomeIcon icon={faItchIo} size="2x" color="#d8d8d8"/></a>
                        <a href="https://github.com/minwater2"><FontAwesomeIcon icon={faGithub} size="2x" color="#d8d8d8"/></a>
                    </div>  
                    <div class="contact">
                        <span><FontAwesomeIcon icon={faEnvelopeSquare} size="lg" color="#d8d8d8" pull="left"/>minsuthekoiwi@gmail.com</span>
                        <span><FontAwesomeIcon icon={faLocationArrow} size="lg" color="#d8d8d8" pull="left"/>Christchurch, New Zealand</span>
                        <span><FontAwesomeIcon icon={faDiscord} size="lg" color="#d8d8d8" pull="left"/>Dog#6319</span>
                    </div>
                </div>
            </div>
        </Row>
    )
}
