import '../styles/ProjectOverlayCard.scss';
import Card from 'react-bootstrap/Card';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default function ProjectOverlayCard(project) {
    var classes = "project-overlay-card";
    if (project.gradient) { classes += " gradient"; }

    let content = (project.icon)
        ?
        <>
            <Card.Img src={project.image}/>
            <Card.ImgOverlay className="centered">
                <Card.Img as={FontAwesomeIcon} icon={project.icon} size="4x"/>
                <Card.ImgOverlay>
                    <h3>{project.title}</h3>
                </Card.ImgOverlay>
            </Card.ImgOverlay>
        </>
        :
        <>
            <Card.Img src={project.image}/>
        </>;

    return (
        <NavLink exact to={"/projects/" + project.slug} className={classes}>
            <div className="project-card-body">
                <div className="project-card-image">
                    {content}
                </div>
                <div className="project-card-text">
                    <h5>{project.description}</h5>
                    <h6>{project.subDescription}</h6>
                </div>
            </div>
        </NavLink>
    )
}