import '../../styles/Project.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectTextCard from '../ProjectTextCard';
import ProjectImageCard from '../ProjectImageCard';
import ProjectYoutubeCard from '../ProjectYoutubeCard';
import ItchDownload from '../ItchDownload';

export default function MatchPointTennis() {
    return (
        <div className="project">
            <Row>
                <Col md={12}>
                    <ProjectTextCard>
                        <h2>Match Point Tennis</h2>
                        <h5>Virtual Reality Tennis Game</h5>
                        <a href="https://www.oculus.com/experiences/quest/4182698635109247/?ranking_trace=13034381182[%E2%80%A6]47_SKYLINEWEBQUESTSEARCH_df31f69a-7c8f-47e7-8173-b8b608cc1e56" target="_blank" rel="noopener noreferrer"><h3>Oculus</h3></a>
                        <a href="https://sidequestvr.com/app/4091/match-point-tennis-free-demo" target="_blank" rel="noopener noreferrer"><h3>SideQuest</h3></a>
                    </ProjectTextCard>
                    
                    <ProjectYoutubeCard src="Hm-OuKq6yEk" type="video/mp4" muted="true"/>
                    <ItchDownload src='863251' download='https://backyardgames.itch.io/match-point-tennis' title='Match Point Tennis by backyardgames, minwater2' />
                </Col>
            </Row>

            <Row>
                {/* ------ Left Column ------ */}
                <Col md={8}>
                    <ProjectTextCard>
                        <h2>Summary</h2>
                        <br />
                        <h3>This was a studio's side project and we worked on it during down time.</h3>
                        <h3>It was my very first professional game! Needless to say, I was super excited to work on it.</h3>
                    
                        <br />
                        <h4>Accomplishments:</h4>
                        <ul>
                            <li>Re-created real life tennis experience into a virtual game</li>
                            <li>Implemented targets mode that is engaging and have a high replayability</li>
                            <li>Implemented easily customizable difficulty profiles to ensure new players can still have a good hit and provide good challenge for skilled players</li>
                            <li>Implemented tutorial for new players that is engaging and helpful</li>
                            <li>Implemented multiple locomotion methods</li>
                            <li>Assisted in designing and developing an AI profiles that are fair, fun to play against and challenging</li>
                            <li>Created editor tools to shorten debugging time and visualize game data </li>
                            <li>Implemented networking functionality using Photon Bolt and Photon PUN2</li>
                        </ul>

                        <br />
                        <h4>Learnings:</h4>
                        <ul>
                            <li>Working on a side project was very productive way to spend the down time </li>
                            <li>There are a lot more things to consider for online multiplayer games vs local multiplayer games (E.g. regions, public lobby, voice chat, etc)</li>
                            <h3>I have better understanding of how Unity Engine handles physics and I have a new respect for people who make custom physics games.</h3>
                            <h3>Re-creating tennis physics simulation was very challenging but it has also deepened my understanding of physics in general. </h3>
                            <li>Try to avoid modifying 3rd party asset's scripts. Creating a new script and modifying it instead will at least allow you to update the asset without conlficts. </li>
                            <li>Building a testing tool so that you don't always have to test features on VR allowed us to save time on building and we felt less fatigued at the end of the day.</li>
                            <li>Attaching a debugger to the build will help you find that nasty bug that only happens on build.</li>

                        </ul>

                        {/* <h3>Technical breakdowns of few features and more details of few learnings can be found below.</h3> */}
                    </ProjectTextCard>
                </Col>

                {/* ------ Right Column ------ */}
                <Col md={4}>
                    <ProjectTextCard>
                        <h2>Studio</h2>
                        <ul>
                            <li>Developed By Backyard Games</li>
                            <li>Role: Gameplay/Network Programmer</li>
                            <li>Christchurch </li>
                            <li>Size: Very Small (&lt; 5)</li>
                        </ul>
                    </ProjectTextCard>
                
                    <ProjectTextCard>
                        <h2>Technologies</h2>
                        <ul>
                            <li>Unity</li>
                            <li>C#</li>
                            <li>.NET</li>
                            <li>Photon</li>
                            <li>Oculus</li>
                        </ul>
                    </ProjectTextCard>

                    <ProjectImageCard src="/images/mpt/playing.gif" caption="Juggling"/>
                    {/* <ProjectYoutubeCard src="nNBo2wARGPE"/> */}
                </Col>
            </Row>

            <Row> 
                <Col md={4}>
                    <ProjectImageCard src="/images/mpt/ai.gif" caption="Playing Aginst AI"/>
                    <ProjectImageCard src="/images/mpt/serve.gif" caption="Serving"/>
                </Col>
                <Col md={8}>
                    <ProjectYoutubeCard src="nNBo2wARGPE"/>
                    <ProjectImageCard src="/images/mpt/balls.jpg"/>
                </Col> 
            </Row>
            
            {/* <Row> <Col md={12}><ProjectTextCard> <h2>Physics</h2> </ProjectTextCard></Col> </Row>
            
            <Row>
                <Col md={6}>
                    <ProjectTextCard>
                        <h3>Match Point Tennis aimed to be easy and intuitive to play but challenging and fun to master.</h3>
                        <h3>We have achieved this by building a custom physics solution and implementing difficulty profiles. 
                            There are 3 difficulty profiles in the game. Easy, Normal, and Hard.
                            Strength of projectile correction changes based on what difficulty profile players are on.
                            Easy = High tolerance of correction, Hard = little to no correction</h3>
                        <br />
                        <h3>Here's how the correction works.</h3>
                        <ul>
                            <li>First we predict where the ball would land from the initial hit</li>
                            <li>Check if the landing spot is within the correction threshold (both vertical and horizontal angle)</li>
                            <li>If the hit is within the correction threshold then we apply correction based on the profile</li>
                            <li>If not then the correction does not get applied</li>
                        </ul>

                        <br />
                        <h4>What I've learned</h4>
                        <h3>Although turn based-combat is pretty straightforward to implement, we ran into multiple issues when implemented networking functionalities using Mirror.</h3>
                        <h3>For example, in order to prevent cheating, we didn't send spectators all the battle data when they start specatating. 
                            We only shared the public data such as tames that are on the field and their states. 
                            Private data such as states of tames that have been't summoned yet were not shared to the spectators. 
                            This way, spectators couldn't just start specatating a battle and give vital informations to participants that could potentially affect the outcome of the battle.</h3>
                    </ProjectTextCard>
                </Col>
                
                <Col md={6}> <ProjectYoutubeCard src="nNBo2wARGPE"/></Col>
            </Row> */}

            
        </div>
    )
}
