import '../../styles/Project.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectTextCard from '../ProjectTextCard';
import ProjectImageCard from '../ProjectImageCard';
import ProjectYoutubeCard from '../ProjectYoutubeCard';

export default function Goleman() {
    return (
        <div className="project">
            <Row>
                <Col md={12}>
                    <ProjectTextCard>
                        <h2>Goleman Group VR Learning Experiences</h2>
                        <h5>Virtual Reality Training Experience</h5>
                        <a href="https://evanstaylor.digital/golman-group-vr/" target="_blank" rel="noopener noreferrer"><h3>ETD Website</h3></a>
                        <a href="https://goleman.co.nz/training/" target="_blank" rel="noopener noreferrer"><h3>Goleman Website</h3></a>
                    </ProjectTextCard>
                    
                    <ProjectYoutubeCard src="2CzJWYEh8vw" type="video/mp4" muted="true"/>
                </Col>
            </Row>

            <Row>
                {/* ------ Left Column ------ */}
                <Col md={8}>
                    <ProjectTextCard>
                        <h2>Summary</h2>
                        <br />
                        <h3>During my employment at ETD, I have had the opportunity to create few VR training experiences.</h3>
                        <h3>Although, the apps were suppose to be educational. We did our best to make the apps engaging and we tried to utilize the strength of VR as much as possible.</h3>

                        <h3>It was my first time having to interact with clients, developing products based on their requirements 
                            and dealing with deadlines for the product.</h3>
                        <h3>As an aspiring game developer, developing VR apps was a fantastic opportunity for me to develop technical skills and deepen my understanding of product development process. </h3>
                    
                        <br />
                        <h4>Accomplishments:</h4>
                        <ul>
                            <li>Developed multiple virtual reality training experiences that were tailored to users who never tried VR apps before</li>
                            <li>Implemented a quiz system that supported multi choices and pictures </li>
                            <li>Implemented a subtitles system that got extended to support multi-languages </li>
                            <li>Implemented a chapter system using Unity's Timeline asset</li>
                            <li>Implemented a engaging tutorial system for users who never tried VR apps before </li>
                        </ul>

                        <br />
                        <h4>Learnings:</h4>
                        <ul>
                            <li>I have learnt the importance of time management, being agile, and do's and don'ts for when developing VR apps.</li>
                            <li>I have also learnt the importance of work culture and how it can affect your work motivation</li>
                            <li>While this wasn't a game development, the skills that I have learned from this project could easily transfer to game development</li>
                            <li>Optimization is very important for VR apps as VR devices aren't as powerful as PC devices</li>
                            <li>VR Training is a very effective practical learning method </li>
                            <li>3rd Party assets are very useful for quick prototypes</li>
                            <li>Avoid modifying 3rd party asset's scripts. Creating a new script and modifying it instead will at least allow you to update the asset easily </li>
                            <li>Try building a testing tool so that you don't always have to test features on VR. This will save time and you will be less fatigued</li>
                            <li>Things that seem to work in editor will sometimes not work on build. Attaching a debugger to the build will definitely speed up the process</li>
                        </ul>

                        {/* <h3>Technical breakdowns of few features and more details of few learnings can be found below.</h3> */}
                    </ProjectTextCard>
                </Col>

                {/* ------ Right Column ------ */}
                <Col md={4}>
                    <ProjectTextCard>
                        <h2>Studio</h2>
                        <ul>
                            <li>Developed By Evans Taylor Digital</li>
                            <li>Role: Unity Developer</li>
                            <li>Christchurch </li>
                            <li>Size: Very Small (&lt; 5)</li>
                        </ul>
                    </ProjectTextCard>
                
                    <ProjectTextCard>
                        <h2>Technologies</h2>
                        <ul>
                            <li>Unity</li>
                            <li>C#</li>
                            <li>.NET</li>
                            <li>Oculus</li>
                        </ul>
                    </ProjectTextCard>
                    <ProjectImageCard src="/images/goleman/training.jpg" caption="Middle of Training"/>
                    {/* <ProjectYoutubeCard src="nNBo2wARGPE"/> */}
                </Col>
            </Row>

            <Row> 
                <Col md={4}>
                    <ProjectImageCard src="/images/goleman/image1.gif" caption="Interactable Tools"/>
                    <ProjectImageCard src="/images/goleman/image2.gif" />
                </Col>
                <Col md={8}>
                    <ProjectYoutubeCard src="Fgpdo35Ttok"/>
                    <ProjectImageCard src="/images/goleman/image3.gif"/>
                </Col> 
            </Row>

        </div>
    )
}
