import '../../styles/Project.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectTextCard from '../ProjectTextCard';
import ProjectImageCard from '../ProjectImageCard';
import ProjectVideoCard from '../ProjectVideoCard';
import ItchDownload from '../ItchDownload';

export default function SlashAndDash() {
    return (
        <div className="project">
            <Row>
                <Col md={12}>
                    <ProjectTextCard>
                        <h2>Slash And Dash</h2>
                        <h5>Action Wave Survival Game</h5>
                    </ProjectTextCard>
                    
                    <ProjectImageCard src="/images/slashAndDash/newDash1.gif"/>
                    <ItchDownload src='645744' download='https://minwater2.itch.io/slash-and-dash' title='Slash And Dash by minwater2' />
                </Col>
            </Row>

            <Row>
                {/* ------ Left Column ------ */}
                <Col md={8}>
                    <ProjectTextCard>
                        <h2>Summary</h2>
                        <br />
                        <h3>This game was my very first personal project.</h3>
                        <h3>The goal was to see how much I can push the limit of dashing combat.</h3>
                        <h3>The dashing ability was inspired from Hyper Light Drifter and the instant death combat was inspired by Katana Zero.</h3>

                        <h4>Accomplishments:</h4>
                        <ul>
                            <li>Variety of enemies each providing unique experiences to the player</li>
                            <li>Easily configurable wave system using ScriptableObject</li>
                            <li>Boss using simple state machine AI with multiple attack patterns</li>
                            <li>Didn't give up and actually finished the project</li>
                        </ul>

                        <h4>Learning:</h4>
                        <ul>
                            <li>Developing a prototype is one of the best way to deepen your understanding of game development</li>
                            <li>Just because the idea sounds cool, doesn't mean you should add it to the game </li>
                            <li>Getting your game playtested early and collecting feedback early is essential </li>
                            <li>It is very easy for scope creep to happen when you are developing alone. I have had to stop myself multiple times</li>
                        </ul>

                    </ProjectTextCard>
                </Col>

                {/* ------ Right Column ------ */}
                <Col md={4}>
                    <ProjectTextCard>
                        <h2>Technologies</h2>
                        <ul>
                            <li>Unity</li>
                            <li>C#</li>
                            <li>.NET</li>
                        </ul>
                    </ProjectTextCard>
                    <ProjectImageCard src="/images/slashAndDash/doubleDash.gif" caption="Extra Dash"/>
                </Col>
            </Row>

            <Row><Col md={12}><ProjectImageCard src="/images/slashAndDash/Weapons.gif" caption="Weapons Showcase"/> </Col></Row>

            <Row> 
                <Col md={6}>
                    <ProjectImageCard src="/images/slashAndDash/cover.gif" caption="Glaive"/>
                </Col>
                <Col md={6}>
                    <ProjectImageCard src="/images/slashAndDash/boss.gif" caption="Boss"/>
                </Col> 
            </Row>
        </div>
    )
}
