import '../styles/Header.scss';
import { NavLink } from 'react-router-dom';
import {Navbar, Nav, Row} from 'react-bootstrap';

export default function Header() {
    return (
        <div className="header">
            <Navbar expand="md">
                <Row className="top-row">
                    <Navbar.Brand>
                        <div className="title">
                            <Nav.Link exact as={NavLink} to="/">Minsu (Mike) Lee</Nav.Link>
                        </div>
                    </Navbar.Brand>
                </Row>

                <Row>
                    <Navbar.Toggle />
                    <Navbar.Collapse>
                        <Nav>
                            <Nav.Link exact as={NavLink} to="/about" className="nav-item">About Me</Nav.Link>
                        </Nav>
                        <Nav>
                            <Nav.Link exact as={NavLink} to="/projects" className="nav-item">Projects</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Row>
            </Navbar>
        </div>
    )
}
