import '../../styles/Project.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectTextCard from '../ProjectTextCard';
import ProjectImageCard from '../ProjectImageCard';

export default function Wizards() {
    return (
        <div className="project">
                 <Row>
                <Col md={12}>
                    <ProjectTextCard>
                        <h2>WizARds</h2>
                        <h5>Mixed Reality Local Multiplayer Game</h5>
                        <a href="https://www.hitlabnz.org/" target="_blank" rel="noopener noreferrer"><h3>HIT Lab NZ Website</h3></a>
                    </ProjectTextCard>
                    
                    <ProjectImageCard src="/images/wizards/cover.png"/>
                </Col>
            </Row>

            <Row>
                {/* ------ Left Column ------ */}
                <Col md={8}>
                    <ProjectTextCard>
                        <h2>Summary</h2>
                        <br />
                        <h3>WizARds is a mixed reality local multiplayer game where you can shoot at other players while also trying to catch a snitch that is controlled by the audiences playing on PC.</h3>
                        <br />
                        <h3>This was my very first AR and multiplayer project!</h3>
                        <h3>I ran into little bit of trouble learning a whole new technology and creating my very first multiplayer experience but it was still very fun project to be part of 
                            and I would love to see what others can do with this exciting technology. </h3>
                    
                        <br />
                        <h4>Accomplishments:</h4>
                        <ul>
                            <li>Designed and developed a mixed reality local multiplayer game that involves physical activity</li>
                            <li>Designed a core game loop that was simple and engaging to play</li>
                            <li>Implemented the "Twitch Plays Pokemon" like snitch controller for spectators (Sending input and queuing)</li>
                        </ul>

                        <h4>Learning:</h4>
                        <ul>
                            <li>HIT Lab made me appreciate working at friendly and inspiring environment and like-minded people</li>
                            <li>Learned the basic fundamentals of network programming. (sync vars, RPCs, events, handling disconnections and etc)</li>
                            <li>Mixed reality has a huge potential to lead the next generation of entertainment if done right</li>
                            <li>Testing multiplayer features on multiple devices by myself was painful. I wanted to figure out a better way to do it. </li>
                        </ul>

                        <a href="https://docs.google.com/document/d/1ZTcapudgDfTMoltjA8jNheqt9Ii62uUr50XF1L3E6-w/edit#" target="_blank" rel="noopener noreferrer"><h3>Link to the project document</h3></a>
                        {/* <h3>Technical breakdowns of few features and more details of few learnings can be found below.</h3> */}
                    </ProjectTextCard>
                </Col>

                {/* ------ Right Column ------ */}
                <Col md={4}>
                    <ProjectTextCard>
                        <h2>Studio</h2>
                        <ul>
                            <li>HIT Lab NZ</li>
                            <li>Role: Summer Internship</li>
                            <li>Christchurch </li>
                        </ul>
                    </ProjectTextCard>
                
                    <ProjectTextCard>
                        <h2>Technologies</h2>
                        <ul>
                            <li>Unity</li>
                            <li>C#</li>
                            <li>.NET</li>
                            <li>Magic Leap</li>
                        </ul>
                    </ProjectTextCard>
                </Col>
            </Row>

            <Row> 
                <Col md={4}>
                    <ProjectImageCard src="/images/wizards/shooting.gif" caption="Shooting"/>
                </Col>

                <Col md={4}>
                    <ProjectImageCard src="/images/wizards/blocking.gif" caption="Blocking"/>
                </Col>

                <Col md={4}>
                    <ProjectImageCard src="/images/wizards/catching.gif" caption="Catching Snitch"/>
                </Col>
            </Row>
        </div>
    )
}
