import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { Container, Row } from "react-bootstrap";

// Font awesome icons
import {library} from '@fortawesome/fontawesome-svg-core';
import { fas } from '@fortawesome/free-solid-svg-icons'


// Stand alone components
import Header from "./components/Header";

// Nav page components
import Projects from "./components/pages/Projects";
import AboutMe from "./components/pages/AboutMe";

// Project components
import SlashAndDash from "./components/projects/SlashAndDash";
import UntamedIsles from "./components/projects/UntamedIsles";
import WolfWolfSheep from "./components/projects/WolfWolfSheep";
import MatchPointTennis from "./components/projects/MatchPointTennis";
import Goleman from "./components/projects/Goleman";
import Wizards from "./components/projects/Wizards";
import Extras from "./components/projects/Extras";

// Font awesome setup
// const iconList = Object.keys(Icons)
//   .filter((key) => key !== 'fas' && key !== 'prefix')
//   .map((icon) => Icons[icon]);

library.add(fas);

export default function App() {
  return (
    <Router>
      <Container fluid className="main">
        {/* Adds page header to all pages */}
        <Header />
      </Container>

      <Container>
        <Row className="justify-content-center">
          {/* Registers route listeners */}
          <Switch>
            <Route path="/" exact component={() => <Projects />} />
            <Route path="/projects" exact component={() => <Projects />} />
            <Route path="/about" exact component={() => <AboutMe />} />

            {/* Projects */}
            <Route path="/projects/untamedisles" exact component={() => <UntamedIsles />} />
            <Route path="/projects/mpt" exact component={() => <MatchPointTennis />} />
            <Route path="/projects/goleman" exact component={() => <Goleman />} />
            <Route path="/projects/wizards" exact component={() => <Wizards />} />
            <Route path="/projects/wolfwolfsheep" exact component={() => <WolfWolfSheep />} />
            <Route path="/projects/slashanddash" exact component={() => <SlashAndDash />} />
            <Route path="/projects/extras" exact component={() => <Extras />} />
          </Switch>
        </Row>
      </Container>
    </Router>
  );
}
