import '../../styles/Project.scss';
import {Row, Col} from 'react-bootstrap';
import ProjectTextCard from '../ProjectTextCard';
import ProjectImageCard from '../ProjectImageCard';
import ProjectVideoCard from '../ProjectVideoCard';

export default function UntamedIsles() {
    return (
        <div className="project">

             <Row>
                <Col md={12}>
                    <ProjectTextCard>
                        <h2>Untamed Isles</h2>
                        <h5>Monter Taming MMORPG</h5>
                        <a href="https://untamedisles.com/" target="_blank" rel="noopener noreferrer"><h3>Official Website</h3></a>
                        <a href="https://store.steampowered.com/app/1823300/Untamed_Isles/" target="_blank" rel="noopener noreferrer"><h3>Steam</h3></a>
                    </ProjectTextCard>

                    <ProjectVideoCard src="/videos/untamed/trailer.mp4" type="video/mp4" muted="true"/>
                </Col>
            </Row>

            <Row>
                {/* ------ Left Column ------ */}
                <Col md={8}>
                    <ProjectTextCard>
                        <h2>Summary</h2>
                        <br />
                        <h3>I joined the studio's very first project just after they've had an alpha combat test.</h3>
                        <h3>I was assigned to work on combat since <b>Day 1</b> and became the "combat" guy</h3>
                        
                        <br />
                        <h4>Accomplishments:</h4>
                        <ul>
                            <li>Developed non-instanced turn-based combat system that can be spectated real-time</li>
                            <li>Networked combat events with anti-cheat in mind</li>
                            <li>Architected skill system with long-term scalability in mind</li>
                            <li>Handled merge requests to ensure implemented feature is written cleanly (modular, extensible, readable, etc) and properly networked. </li>
                            <li>Collaborated with designers to help design and develop skills</li>
                            <li>Developed editor tools for content creation</li>
                            <li>Collaborated with artists to implement UI elements</li>
                            <li>Ran play testing session to validate game features and make improvements based on feedback</li>
                            <li>Implemented networking functionalities using Mirror Networking library</li>
                        </ul>

                        <br />
                        <h4>Learnings:</h4>
                        <ul>
                            <li>Working at a large scale company was challenging. 
                                I have learnt the importance of like-minded people, development pipelines and communication. </li>
                            <li>Working on a large scale game constantly pushed my technical skills to the limit 
                                and my depth of knowledge of programming rapidly grew. </li>
                            <li>Always test your feature in the build especially if it is networked</li>
                            <li>Lack of communication can really hurt the project </li>
                            <li>Find the fun from MVP and build on top of it but don't bloat the game with features</li>
                            <li>Peer programming is very powerful when done right</li>
                            <li>Playtesting is essential for validating features</li>
                            <li>Know the limitations of the plugins that you are using</li>
                            <li>Don't be afraid to talk to your team and seek advice if you are stuck</li>
                            <li>Lazily implementing things will come back and bite you. Proper implementation is vital for reducing technical debts. </li>
                            <li>Chip away at technical debts whenever you can</li>
                        </ul>

                        <h3>Technical breakdowns of few features and more details of few learnings can be found below.</h3>
                    </ProjectTextCard>
                </Col>

                {/* ------ Right Column ------ */}
                <Col md={4}>
                    <ProjectTextCard>
                        <h2>Studio</h2>
                        <ul>
                            <li>Developed By Phat Loot Studios</li>
                            <li>Role: Gameplay/Network Programmer</li>
                            <li>Fully Remote (Most NZ Based)</li>
                            <li>Size: Large (&gt; 50)</li>
                        </ul>
                    </ProjectTextCard>
                
                    <ProjectTextCard>
                        <h2>Technologies</h2>
                        <ul>
                            <li>Unity</li>
                            <li>C#</li>
                            <li>.NET</li>
                            <li>Mirror</li>
                            <li>Unity ECS</li>
                            <li>Firebase</li>
                        </ul>
                    </ProjectTextCard>

                    <ProjectVideoCard src="/videos/untamed/tames.mp4" type="video/mp4" muted="true" loop="true" caption="Tame Showcase"/>
                </Col>
            </Row>
            
            <Row> <Col md={12}><ProjectTextCard> <h2>Combat</h2> </ProjectTextCard></Col> </Row>

            <Row>
                <Col md={6}>
                    <ProjectTextCard>
                        <h3>The game features non-instanced turn-based combat that can be spectated real-time.</h3>
                        <h3>I have collaborated with a colleague who architected and implemented battle system while I architected and implemented the skills system.</h3>
                        <br />
                        <h4>What I've learned</h4>
                        <h3>Although turn based-combat is pretty straightforward to implement, we ran into multiple issues when implemented networking functionalities using Mirror.</h3>
                        <h3>For example, in order to prevent cheating, we didn't send spectators all the battle data when they start spectating. 
                            We only shared the public data such as tames that are on the field and their states. 
                            Private data such as states of tames that have been't summoned yet were not shared to the spectators. 
                            This way, spectators couldn't just start spectating a battle and give vital informations to participants that could potentially affect the outcome of the battle.</h3>
                    </ProjectTextCard>
                </Col>
                <Col md={6}> <ProjectVideoCard src="/videos/untamed/combat.mp4" type="video/mp4" muted="true" loop="true" caption="Combat Teaser"/></Col>
            </Row>

            <Row> <Col md={12}><ProjectTextCard> <h2>Skills</h2> </ProjectTextCard></Col> </Row>

            <Row>
                <Col md={5}><ProjectImageCard src="/images/untamed/combatSystems.png" /> </Col>

                <Col md={7}>
                    <ProjectTextCard>
                        <h3>Instead of having 100+ different monsters like Pokemon, Untamed Isles had procedurally generated tames using 8 base tames with 7 different body parts that can be mixed and matched.</h3>
                        <h3>Each base tame has a set of generic attack, defence and utility skills 
                            and a set of attack, defence and utility skills that utilizes a powerful mechanic. </h3>
                        <h3>For example, Brackis has a mechanic called Truestrike that made tame's next attack skill ignore the target's defence skill</h3>
                        <h3>So players are encouraged to build their own unique tame by mixing and matching multiple different body parts 
                            which would result in a tame with an unique combination of powerful mechanics.</h3>
                        <br />

                        <h4>What I've learned</h4>
                        <h3>Balancing the right number of skills and number of unique mechanics per base tames was tricky because more skills meant more customization for players. 
                            Originally each base tame had two or more unique mechanics but through playtesting we found out 
                            that having a huge number of skills being available can feel quite overwhelming.</h3>

                        <h3>So we have reduced the number of unique mechanic to just one per base tame. 
                            This strengthened tame's identity and combat felt less overwhelming.
                            It also made skill mechanics easier to maintain, balance and scalable for programmers, designers, and artists.</h3>
                    </ProjectTextCard>
                </Col>
            </Row>

            <Row>
                <Col md={5}>
                    <ProjectTextCard>
                        <h3> Here are some considerations I had when I architected the skill system: </h3>
                        <ul>
                            <li>Make sure skills are easily modifiable</li>
                            <li>Reusability - multiple skills can use the same skill effects</li>
                            <li>Execution order for skill effects - some skill effects happen before or after skill execution </li>
                            <li>Animation, Particles and audio for skills</li>
                            <li>Managing states of skill effects per tame - each tame needs to track their own skill effect's state</li>
                            <li>Persistency - some skill effects would persist even when tames are no longer on the field</li>
                            <li>Modularity - make sure skill effect's controlling logic isn't all handled in one god class</li>
                        </ul>

                        <h4>What I've learned</h4>
                        <h3>ScriptableObject is very useful for holding data.</h3> 
                        <h3>Storing the data for skills in ScriptableObject allowed us to easily modify skills using the inspector and live-update when needed </h3>
                        <h3>Interfaces are very powerful when used correctly.</h3> 
                        <h3>Implementing skill effects as interfaces allowed them to be reusable and extensible 
                            and implementing skill effects to extend multiple interfaces such as IPreEffect and IPostEffect allowed us to easily trigger multiple skill effects in correct order</h3>
                        <h3>MVC pattern is a powerful pattern.</h3> 
                        <h3>Model = data, View = UI, Control = SkillProcessor Propagating the controlling logic of skill effects to their own processors made maintaining and modifying the logic very easy.</h3>
                    </ProjectTextCard>
                </Col>
                <Col md={7}>
                    <ProjectImageCard src="/images/untamed/skillDiagram.png" caption="Skill Class Diagram"/> 
                </Col>
            </Row>

            <Row>
                <Col md={6}> <ProjectImageCard src="/images/untamed/timelines.png" caption="Timeline example"/> </Col>
                <Col md={6}> 
                    <ProjectTextCard>
                            <h3>We have decided to use Unity's Timeline asset to handle the visuals of skills. The timeline stored data for monster's animation, particle effects, and FMOD audio tracks. 
                                ScriptableObject simply stored the reference of the timeline object and the GameObject containing the timeline was instantiated when needed </h3>
                            
                            <h3>Animation data was populated based on tame's anatomy</h3>
                            <h3>Tame's audio changed based on tame's head</h3>
                            <h3>Dynamic timeline was populated based on whether the target had defended or not when playing attack skill timelines</h3>

                            <br />
                            <h4>What I've learned</h4>
                            <h3>Timeline allowed artists to have complete freedom to create visuals and programmers just had to make sure it is networked </h3> 
                            <h3>Although timelines are easy to modify because the PlayableGraph is an asset and it can't be a prefab, 
                                changing the structure of the template meant modifying 100s of timeline assets so planning ahead was definitely necessary  </h3> 
                            <h3> There are no updated documentation on how to write custom timeline tracks. Learning how to write custom timeline tracks wasn't as straightforward.</h3>
                        </ProjectTextCard>
                </Col>
            </Row>
        </div>
    )
}
